
const host = "http://192.168.0.18/xykfpd_back/public";
const app = {
	    apiUrl:host, //
		baseRequest(obj) {
			try {
				const userToken = uni.getStorageSync('userToken');
				if (userToken) {
					if (obj.header) {
						obj.header["Authorization"] = userToken;
					} else {
						obj.header = {
							"Authorization": userToken
						};
					}
					console.log("1111111");
					obj.url = this.apiUrl + obj.url;
					uni.request(obj)
				} else {
					console.log("userToken")
 
				}
			} catch (e) {
				console.log(e)
				console.log("userToken")
			}
		},
	}

module.exports = {
  host,app
}